.workSpace {
  position: relative;
  overflow-y: scroll;
  display: flow-root;
  width: 100%;
  height: 510px;
  background-color: #2f4f4f;
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
  .workSpace {
    height: 480px;
  }
}
@media screen and (min-width: 1280px) {
  .workSpace {
    height: 546px;
  }
}
